@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);
@import url(https://clean-u-i.netlify.app/component/component.css);
@import url(https://fonts.googleapis.com/css2?family=Creepster&family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);



:root{
  --primary-color:#1e1e1e;
  --secondary-color:rgb(248, 245, 245);
  --pink-color: #fd5056;
  --dark-pink:#f50057;
  --red:rgb(246, 6, 6);
  --light-color:white;
  --lighter-white-color:#f0f8ff;
  --dark-color:rgba(0, 0, 0, 0.87);
  --shadow-color:rgb(0 0 0 / 5%);
  --card-shadow: 0px 0px 10px 4px rgb(0 0 0 / 5%);
  --bg-color:#f3f2f2;
  --shadow1:-3px -3px 5px rgba(255,255,255, 0.432),3px 3px 5px rgba(1,1,1, 0.432);
  --dropdown-bg:#ddd;
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
}
.list-style-none{
  list-style: none;
}
.flex-col{
  display: flex;
  flex-direction: column;
}
.flex-wrap{
  flex-wrap: wrap;
}
a{
  text-decoration: none;
}
.text-pink{
  color:#f50057;
  color:var(--dark-pink);
}
.text-grey{
  color:var(--dark-grey);
}
.text-red{
  color:rgb(246, 6, 6);
  color:var(--red);
}
.text-center{
  text-align: center;
}
ul{
  margin:0;
  padding: 0;
}
.font-md{
  font-size: 0.8rem;
}
.flex-row{
  display: flex;
}
.icon-white{
  color:white;
  color:var(--light-color);
}
.space-1rem{
  width:100%;
  height:1rem;
}
.space-2rem{
  width:100%;
  height:2rem;
}
.space-3rem{
  width:100%;
  height:3rem;
}
.space-4rem{
  width:100%;
  height:4rem;
}
.space-5rem{
  width:100%;
  height:5rem;
}
.mx-3{
  margin: 0 2rem;
}
.justify-center{
  justify-content: center;
}
.align-center{
  align-items:center;
}
.flex-wrap{
  flex-wrap:wrap;
}
.justify-between{
  justify-content: space-between;
}
.justify-evenly{
  justify-content: space-evenly;
}
.justify-around{
  justify-content: space-around;
}
.gap-1{
  grid-gap:1rem;
  gap:1rem;
}
.w-100{
  width:100%;
}
.inline-block{
  display: inline-block;
}
.inline{
  display: inline;
}
a:hover{
  color:#f50057;
  color:var(--dark-pink);
}
.text-xxs{
  font-size:0.8rem;

}
.home-container{
    display: grid;
    grid-template-columns: 230px auto;
    height:85vh;
}
@media only screen and (max-width:840px){
    .home-container{
        display: block;
    }
}
.hero-section-container {
  width: 100%;
  height: 85vh;
  background-color: var(--dark-color);
}
.hero-image {
  position: relative;
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
  min-height: 85vh;
  width: 100%;
  height: auto;
  -webkit-filter: brightness(30%);
          filter: brightness(30%);
}
.text-overlay-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: var(--secondary-color);
}
.explore-btn {
  color: var(--pink-color);
  background-color: var(--primary-color);
  border-radius: 0.5rem;
  border: 2px solid var(--pink-color);
  margin-top: 1rem;
}
.explore-btn:hover {
  background-color: var(--pink-color);
  color: var(--secondary-color);
}
.overlay-text {
  font-family: "Creepster", cursive;
  font-size: 5vw;
}
.explore-text {
  color: var(--pink-color);
  font-family: "Creepster", cursive;
}
@media(max-width:840px){
    .overlay-text{
        font-size: 8vw;
    }
}
@media(max-width:640px){
    .overlay-text{
        font-size: 10vw;
    }
}
.sidebar-items {
  display: flex;
  flex-direction: column;
  background-color: var(--primary-color);
}
.sidebar-item {
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  color: var(--secondary-color);
}
.sidebar-item:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}
.active {
  border-bottom: 2px solid var(--pink-color);
  border-radius: 1rem;
}
@media only screen and (max-width: 840px) {
  .sidebar-items{
    display: flex;
    flex-direction:row ;
    justify-content: space-between;
    position: fixed;
    bottom:0;
    left: 0;
    width: 100%;
    z-index: 2;
  }
  .sidebar-item{
      flex-direction: column;
  }
}
@media only screen and (max-width:640px){
    .sidebar-item{
        padding: 1rem 0.5rem;
        font-size: 1rem;
    }
}
.header-container {
  display: flex;
  width: 100%;
  height: 15vh;
  padding: 2rem;
  position: -webkit-sticky;
  position: sticky;
  justify-content: space-between;
  align-items: center;
  background-color: var(--primary-color);
}
.ham-icon {
  color: var(--secondary-color);
  margin-right: 0.5rem;
}
.logo-text1 {
  color: var(--secondary-color);
  font-size: 1.5rem;
}
.logo-text2 {
  color: var(--pink-color);
  font-size: 1.5rem;
}
.searchBar-container {
  position: relative;
  display: flex;
  flex: 1 1;
  align-items: center;
  margin: 0 4rem;
}
.search-icon {
  position: absolute;
  left: 10px;
}
.search-input {
  padding: 0.4rem;
  padding-left: 2rem;
  width: 100%;
  border-radius: 0.5rem;
}
.search-input:focus {
  outline: none;
}
.login-button {
  color:var(--pink-color);
  background-color: var(--primary-color);
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  font-size: 1rem;
  font-weight: 600;
  border: 2px solid var(--pink-color);
  cursor: pointer;
}
.login-button:hover{
    background-color: var(--pink-color);
    color: var(--secondary-color);
}
@media (max-width: 650px) {
  .searchBar-container {
    margin: 0 1rem;
  }
  .header-container {
    padding: 2rem 0.8rem;
  }
  .logo-text1,.logo-text2{
      font-size: 1.2rem;
  }
}

